.section-offset {
  padding: 2.5rem 0;
}

.section-offset-footer {
  padding: 3.75rem 0 1.5625rem;
}

.header {
  &__icons {
    display: flex;
    align-items: center;
    gap: 1rem;
  }

  &__link-swiper {
    font-weight: 400;
    font-size: 1rem;
    color: #726e8d;
  }
}

.navbar {
  &__link {
    font-weight: 400;
    font-size: 1rem;
    color: #726e8d;
    transition: 0.2s ease-in-out;

    @include hover {
      color: #201b3f;
    }
  }
}

.for-small-screens {
  @media (min-width: 34.4375rem) {
    display: none;
  }
}

.for-large-screens {
  @media (max-width: 34.375rem) {
    display: none;
  }
}

.button-grey {
  background-color: var(--light-grey);
  transition: 0.2s ease-in-out;

  @include hover {
    background-color: rgba($color: #4e4d93, $alpha: 0.2);
  }
}

.button-rgb {
  background-color: rgba(249, 249, 249, 0.15);
  transition: 0.2s ease-in-out;

  @include hover {
    background-color: rgba($color: #f9f9f9, $alpha: 0.2);
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.375rem;

  @include small-tablet {
    grid-template-columns: repeat(2, 1fr);
  }

  @include mobile {
    gap: 1.25rem 1rem;
  }
}

.grid-advantages {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.375rem;

  @media (max-width: 59.375rem) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 34.375rem) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.grid-footer {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;

  @media (max-width: 1070px) {
    grid-template-columns: repeat(6, 1fr);
    row-gap: 50px;
  }
}

.form {
  display: flex;

  &__input {
    padding: 1.0625rem 2rem;
  }

  &__btn {
    padding: 1rem 2rem;

    width: 100%;
    max-width: 7.375rem;

    font-weight: 400;
    font-size: 1rem;
    line-height: 150%;
  }

  &__btn-white {
    color: var(--dark-primary);
    background-color: var(--white);

    transition: 0.2s ease-in-out;

    @include hover {
      background-color: var(--light-grey);
    }
  }
}

.input-white {
  background-color: var(--white);
  color: black;
}

.input-grey {
  background-color: rgba(255, 255, 255, 0.15);
  color: var(--white);
  &::placeholder {
    color: var(--white);
  }

  &::-moz-placeholder {
    color: var(--white);
  }
}

.footer {
  background-color: var(--dark-primary);

  &__title {
    margin: 0;

    font-weight: 400;
    font-size: 1rem;
    color: var(--white);
  }

  &__link {
    font-weight: 400;
    font-size: 0.875rem;
    color: var(--white);
  }
}

.social-linkedin {
  background: url(".././img/svg/Logo--linkedin.svg") center center no-repeat;
}

.social-facebook {
  background: url(".././img/svg/Logo--facebook.svg") center center no-repeat;
}

.social-instagram {
  background: url(".././img/svg/Logo--instagram.svg") center center no-repeat;
}

.social-skype {
  background: url(".././img/svg/Logo--skype.svg") center center no-repeat;
}

.social-twitter {
  background: url(".././img/svg/Logo--twitter.svg") center center no-repeat;
}

.social-pinterest {
  background: url(".././img/svg/Logo--pinterest.svg") center center no-repeat;
}
