.footer {
  background-color: var(--dark-primary);

  &__container {
  }

  &__list {
    display: flex;

    flex-direction: column;
    grid-column: 2 span;
    gap: 12px;

    @media (max-width: 420px) {
      grid-column: 3 span;
    }
  }

  &__item {
  }

  &__email {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    grid-column: 6 span;
  }

  &__signature {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 1rem;
    color: var(--white);
  }

  &__hr {
    margin: 30px 0 0;

    border: 0.0625rem solid var(--primary);
  }

  &__bottom {
    margin-top: 1.375rem;

    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      justify-content: center;
    }
  }

  &__copyright {
    font-weight: 400;
    font-size: 0.875rem;
    color: var(--white);
  }

  &__social-bottom {
    display: flex;
    align-items: center;
    gap: 1.5rem;

    @include mobile {
      display: none;
    }
  }
}

.form {
  &__input-footer {
    width: 100%;

    font-weight: 400;
    font-size: 1rem;
  }

  &__btn {
  }
}

.social-bottom__link-icon {
  display: block;
  width: 20px;
  height: 20px;
}

.social-bottom {
  &__item {
  }

  &__link {
  }

  &__link-icon {
  }
}
