.products {
  &__container {
  }

  &__content {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  &__list {
  }

  &__item {
  }

  &__article {
    transition: 0.3s ease-in-out;

    @include hover {
      scale: (1.07);
    }
  }

  &__button {
    margin-top: 3.125rem;
    padding: 1rem 2rem;

    max-width: 10.9375rem;

    font-weight: 400;
    font-size: 1rem;
    line-height: 150%;
    color: var(--dark-primary);

    @include mobile {
      margin-top: 2.5rem;
    }

    @media (max-width: 24.375rem) {
      width: 100%;
    }
  }
}
.article {
  &__text--products {
  }

  &__title--products {
    margin: 0;
    padding: 1.5rem 0 0.5rem;

    font-weight: 400;
    font-size: 1.25rem;
    line-height: 140%;
    color: var(--dark-primary);

    @include mobile {
      padding: 0.5rem 0;

      font-size: 1rem;
    }
  }

  &__price--products {
    margin: 0;

    font-weight: 400;
    font-size: 1.125rem;
    line-height: 150%;
    color: var(--dark-primary);

    @include mobile {
      font-size: 0.875rem;
    }
  }
}
