.footer {
  &__container {
  }

  &__up {
  }

  &__adress {
  }

  &__logo {
  }

  &__social {
    display: flex;
    flex-direction: column;
    gap: 22px;
    grid-column: 3 span;
  }

  &__item-social {
  }

  &__menu {
  }

  &__categories-social {
    @media (max-width: 420px) {
      display: none;
    }
  }

  &__company {
  }

  &__hr {
    margin: 30px 0 16px;
    border: 1px solid var(--primary);
  }

  &__bottom-social {
    text-align: center;
  }

  &__copyright {
    font-weight: 400;
    font-size: 14px;
    color: var(--white);
  }
}

.social {
  &__title {
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 20px);
    gap: 24px;

    @media (max-width: 420px) {
      grid-template-columns: repeat(3, 20px);
    }
  }

  &__item {
  }

  &__link {
    display: block;
    width: 20px;
    height: 20px;
  }
}

.grid-footer {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;

  @media (max-width: 1070px) {
    grid-template-columns: repeat(6, 1fr);
    row-gap: 50px;
  }
}

.social-linkedin {
  background: url(".././img/svg/Logo--linkedin.svg") center center no-repeat;
}
.social-facebook {
  background: url(".././img/svg/Logo--facebook.svg") center center no-repeat;
}
.social-instagram {
  background: url(".././img/svg/Logo--instagram.svg") center center no-repeat;
}
.social-skype {
  background: url(".././img/svg/Logo--skype.svg") center center no-repeat;
}
.social-twitter {
  background: url(".././img/svg/Logo--twitter.svg") center center no-repeat;
}
.social-pinterest {
  background: url(".././img/svg/Logo--pinterest.svg") center center no-repeat;
}
