.subscribe {
  padding: 7.625rem 0;
  background-image: url("../img/Email sign-up.png");
  background-color: brown;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: 500px) {
    padding: 1.5625rem 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1.875rem;
    text-align: center;

    @media (max-width: 500px) {
      align-items: flex-start;
      gap: 1.5625rem;
      text-align: left;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }

  &__title {
    margin: 0;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(1.5rem, 1.281rem + 0.899vw, 2rem);
    line-height: 140%;
    color: var(--white);
  }

  &__info {
    margin: 0;

    font-weight: 400;
    font-size: clamp(0.875rem, 0.765rem + 0.449vw, 1.125rem);
    line-height: 150%;
    color: var(--white);
  }

  &__list {
    display: flex;
    gap: 0.5rem 2.25rem;

    @media (max-width: 500px) {
      flex-direction: column;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }

  &__interest {
    font-weight: 400;
    font-size: 1rem;
    line-height: 150%;
    color: var(--white);
  }

  &__form {
    width: 100%;

    justify-content: center;
  }
}

.form {
  &__input--subscribe {
    width: 100%;
    max-width: 22.1875rem;
  }

  &__btn--subscribe {
    color: var(--white);
    background-color: var(--dark-primary);

    transition: 0.2s ease-in-out;

    @include hover {
      background-color: #242042;
    }
  }
}
