.advantages {
  &__container {
    display: flex;
    flex-direction: column;
    gap: 2.25rem;
  }

  &__title {
    margin: 0;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(1.25rem, 1.14rem + 0.449vw, 1.5rem);
    line-height: 140%;
    text-align: center;
    color: var(--dark-primary);

    @media (max-width: 34.375rem) {
      text-align: left;
    }
  }

  &__content {
  }

  &__list {
  }

  &__item {
    grid-column: 1 span;
    background-color: var(--light-grey);
  }

  &__article {
    padding: 3rem;

    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
}
.article {
  &__svg {
  }

  &__header--advantages {
  }

  &__text--advantages {
  }

  &__title--advantages {
    margin: 0;
    padding-bottom: 0.75rem;

    font-weight: 400;
    font-size: 1.25rem;
    line-height: 140%;
    color: var(--dark-primary);
  }

  &__info--advantages {
    margin: 0;

    font-weight: 400;
    font-size: 1rem;
    line-height: 150%;
    color: var(--dark-primary);
  }
}
