.history {
  &__container {
  }

  &__content {
    display: flex;
    flex-direction: row;
    gap: 1.5rem 0.9375rem;

    @include tablet {
      flex-direction: column;
    }
  }

  &__article {
    padding: 4rem 4.6875rem;

    max-width: 50%;

    display: flex;
    flex-direction: column;

    background-color: var(--dark-primary);

    @include tablet {
      max-width: 100%;
    }

    @include mobile {
      padding: 2.1875rem 2rem;
    }
  }

  &__button {
    margin-top: auto;
    padding: 1rem 2rem;

    max-width: 10.9375rem;

    font-weight: 400;
    font-size: 1rem;
    line-height: 150%;
    color: var(--white);

    @include tablet {
      margin-top: 3.5rem;
    }

    @media (max-width: 24.375rem) {
      max-width: 100%;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    max-width: 100%;
  }
}
.article {
  &__header--history {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }

  &__title--history {
    margin: 0;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(1.25rem, 0.921rem + 1.348vw, 2rem);
    color: var(--white);

    @include mobile {
      line-height: 140%;
    }
  }

  &__info--history {
    margin: 0;

    font-weight: 400;
    font-size: clamp(0.875rem, 0.765rem + 0.449vw, 1.125rem);
    color: var(--white);

    @include mobile {
      line-height: 150%;
    }
  }
}
